import { refs } from './refs';
import axios from 'axios';
import { renderSearchImages } from './render-functions';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

export async function queryPixabay(query, page, perPage) {
  const API_KEY = '48017224-d7e819cca6d5953c75e1fff02';
  axios.defaults.baseURL = 'https://pixabay.com';

  const searchParams = new URLSearchParams({
    q: query,
    image_type: 'photo',
    orientation: 'horizontal',
    safesearch: true,
    per_page: perPage,
    page,
  });

  try {
    const serverResponse = await axios.get(
      `/api/?key=${API_KEY}&${searchParams}`
    );
    // console.log(serverResponse.data);

    const totalImages = serverResponse.data.total;
    const images = serverResponse.data.hits; // –û—Ç—Ä–∏–º—É—î–º–æ –º–∞—Å–∏–≤ –∑–æ–±—Ä–∞–∂–µ–Ω—å
    //console.log('serverResponse.data.hits:', images);

    //console.log('–í—Å—å–æ–≥–æ –∑–æ–±—Ä–∞–∂–µ–Ω—å:', totalImages);

    if (totalImages === 0) {
      iziToast.error({
        title: '–ü–æ–º–∏–ª–∫–∞',
        message: `‚ùå –ù–µ–º–∞—î –∑–æ–±—Ä–∞–∂–µ–Ω—å –∑–∞ –≤–∞—à–∏–º –∑–∞–ø–∏—Ç–æ–º. –°–ø—Ä–æ–±—É–π—Ç–µ —ñ–Ω—à–µ —Å–ª–æ–≤–æ!`,
        position: 'topCenter',
        timeout: 5000,
      });
      refs.btnLoadMore.style.display = 'none';
    }
    //console.log('–ú–∞—Å–∏–≤ –∑–æ–±—Ä–∞–∂–µ–Ω—å:', images);
    renderSearchImages(images);

    return totalImages; // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ –∑–∞–≥–∞–ª—å–Ω—É –∫—ñ–ª—å–∫—ñ—Å—Ç—å –∑–Ω–∞–π–¥–µ–Ω–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
  } catch (error) {
    iziToast.error({
      title: '–ü–æ–º–∏–ª–∫–∞',
      message: `‚ùå –í–∏–Ω–∏–∫–ª–∞ –ø–æ–º–∏–ª–∫–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä—ñ. –°–ø—Ä–æ–±—É–π—Ç–µ —â–µ —Ä–∞–∑ –ø—ñ–∑–Ω—ñ—à–µ!`,
      position: 'topCenter',
      timeout: 5000,
    });
  }
}
